.hero {
  margin: 40px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-top: 50px; */
  gap: 2rem;
  padding-inline: 2rem;
}

.line {
  /* Planet */
  z-index: -1;
  position: absolute;
  min-width: 365px;
  height: 365px;
  right: 15%;
  top: 35%;

  background: linear-gradient(
    180deg,
    #2fb8f2 0%,
    rgba(75, 37, 245, 0) 100%,
    #617ba3 100%
  );
  filter: blur(50px);
  transform: rotate(180deg);
}

.line2 {
  /* Eclipse */
  z-index: -1;
  position: absolute;
  min-width: 500px;
  height: 500px;
  right: 10%;
  top: 35%;

  background: linear-gradient(
    180deg,
    #7b96ff 0%,
    rgba(111, 195, 255, 0.46) 100%
  );
  filter: blur(80px);
  transform: rotate(180deg);
}

.stars1 {
  position: absolute;
  top: 39%;
  right: 32%;
}

.stars2 {
  position: absolute;
  top: 43%;
  right: 31%;
}

.container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  position: relative;
}

.bg {
  max-height: 80%;
  overflow: hidden;
  position: relative;
  z-index: -5;
  bottom: -5%;
  left: 0%;
}

.salut {
  position: relative;

  left: 20px;
  font-family: Manrope;
  font-size: 1rem;
  font-weight: 700;
  line-height: 22.4px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  color: #0e89fc;
}

.smile {
  position: absolute;
  right: 0%;
  top: -5%;
}

.container h1 {
  font-size: 2.5rem;
}

.container .description {
  color: #636571;
}

@media screen and (max-width: 962px) {
  .container h1 {
    font-size: 2rem;
  }

  .container .description {
    color: #636571;
    /* font-size: 0.9rem; */
  }

  .image {
    width: 350px;
  }

  .hero {
    margin: 10px 0px;
  }

  .smile {
    top: 5%;
    right: 15%;
  }
}

.button {
  width: 208px;
  height: 50px;
  border: none;
  background: #0e89fc;
  color: #fbfbfb;
  border-radius: 4px;
  font-family: Manrope;
  font-size: 1rem;
  font-weight: 700;
  line-height: 24px;
  text-align: center;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  display: none;
}

@media screen and (max-width: 735px) {
  .hero {
    flex-direction: column;
    justify-content: center;
    margin-top: 2rem;
    gap: 200px;
  }

  .bg {
    position: relative;
    display: inline;
    bottom: 0px;
  }
  .container2 {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .line,
  .line2 {
    min-width: 300px;
    height: 300px;
    position: absolute;
    top: 30%;
    left: 5%;
  }

  .smile {
    top: -10%;
    right: 10%;
  }

  .salut {
    position: relative;
    left: 0;
  }

  .stars1 {
    position: absolute;
    top: 20%;
    left: 20%;
  }
  .stars2 {
    position: absolute;
    top: 26%;
    left: 32%;
  }

  .image {
    width: 300px;
    height: 400px;
  }

  .button {
    display: block;
  }
}

@media screen and (max-width: 425px) {
  .smile {
    top: 5%;
    right: 15%;
  }
}
